<template>
        <div class="login_wrap">
		<div id="contert">
			<div id="top">
				<div id="top-star">
					<span style="padding-left:20px;">互联网生态圈营销中心</span><br>
				</div>
			</div>
		</div>
		<div id="bot">
            <img src="https://ss3.baidu.com/73V1bjeh1BF3odCf/it/u=3972988672,1075090168&fm=202" />
			<div id="bot-a">
				<p id="bot-b">深恒利登录：</p>
				<div id="bot-bule">
					<form id="form" >
						<table>
							<p>账号：<input type="text" placeholder="请输入账号" v-model="username" /></p>
							<br />
							<p>密码：<input type="password" placeholder="请输入密码"  v-model="password"/></p>
							<p><button class="button" v-on:click="login">登录</button></p>
						</table>
					</form>
					<div id="result"></div>
				</div>
			</div>
		</div>
		<div id="button">
			<p>版权所有：2017深恒利信息技术有限公司</p>
		</div>
        </div>
</template>
<style>
    .login_wrap{text-align:center; height: 1600px; background-image:  url('http://bpic.588ku.com/back_pic/03/65/44/8057ae8dfe9b121.jpg!/fh/300/quality/90/unsharp/true/compress/true')}
    .input{ width:250px; height:80px; line-height:px; margin:0 auto; margin-bottom: px; outline:none;padding:0px; box-sizing:border-box;}
    p{color:red;}
   
    			#contert {
				width: auto;
				height: auto;
			}
			
			#top {
				width: 800px;
				height: 200px;
				margin: auto 0px auto;
				text-align: center;
				/*border: 1px solid red;*/
			}
			
			#top img {
				width: 140px;
				height: 140px;
				float: left;
				padding-left: 60px;
			}
			
			#top-star {
				font-size: 50px;
				float: left;
				color: white;
				line-height: 140px;
			}
			
			#bot {
				width: 800px;
				height: 400px;
				background-color: #aaaaaa;
				margin: 0px auto;
				border-radius: 8px;
				text-shadow: 2px 2px 10px #F0F8FF;
			}
			
			#bot img {
				width: 320px;
				height: 320px;
				float: left;
				padding-left: 40px;
				padding-top: 40px;
			}
			/*右边*/
			
			#bot-a {
				width: 300px;
				height: 200px;
				float: left;
				/*border: 1px solid red;*/
				margin-top: 120px;
				margin-left: 40px;
			}
			
			#bot-a img {
				width: 20px;
				height: 20px;
			}
			
			#bot-b {
				font-size: 30px;
				padding-left: 20px;
				/*padding-top:100px;*/
			}
			
			#bot-bule {
				margin: 20px 0px 0px 20px;
			}
			
			#bot-bule button {
				padding: 6px 26px;
				margin-left: 40px;
				float: left;
				border-radius: 2px;
				background-color: royalblue;
				border: 1px solid royalblue;
			}
			
			#bot-bule button:hover {
				color: white;
			}
			
			#bot-bule input {
				font-size: 16px;
			}
			/*底部*/
			
			.button {
				width: 80%;
				height: 60px;
				margin-top: 40px;
				background-color: aliceblue;
			}
			
			#button p {
				text-align: center;
				line-height: 60px;
			}
</style>
<script>
export default{
	name: 'login',
    data(){
            return{
                username: '',
                password: '',
            }
        },
  methods:{
    login(){
		sessionStorage.obj = this.username;
		 console.log("_username",sessionStorage.obj);
		 let username = this.username
		//console.log("username:" + username)
        if(this.username == "" || this.password == ""){
            alert("请输入用户名或密码")
        }else{
            let data = {'name':this.username,'psw':this.password}
            /*接口请求*/
            this.$http.post('sz800800.cn/index.php/Agency/Login/login',data,{
        headers: {
        },
        emulateJSON: true
    }).then(function(res) {
		 //var userinfo = res.data.userinfo.company; 传值
			if(res.data.status !=-1){
				this.$router.push({ name: 'Home'});
			}else{
				alert("登录失败"+JSON.stringify(res.data.msg))
			}
		  },function(err) {
            console.log("登录失败"+JSON.stringify(err))
          })
      }
    }
  }
  
}
</script>



