<template>
  <div>
    <homeTab></homeTab>
    <h2>充值中心</h2>
    <form class="form-horizontal form-bordered form-row-strippe" id="" data-toggle="validator" enctype="multipart/form-data">

      <p>充值账号： <input class="form-control" v-model="name" readonly="readonly" type="text" value="1111" placeholder="充值账号"></p>
      <p>新合同号： <input class="form-control" v-model="contract_id" type="text" value="" placeholder="新合同号" autocomplete="off"> </p>
      <p>上传合同：<input type="file" @change="onFileChange" id=""></p> 
      <p>金额： <input type="text" v-model="deal_gold"> </p>
      <div class="form-group">
        <label >充值年限：</label>
        <div class="col-md-10">
          <select name="year" class="form-control" v-model="deal_years">
            <option value="">请选择充值年限</option>
            <option value="1">一年</option>
            <option value="2">二年</option>
            <option value="3">三年</option>
            <option value="4">四年</option>
            <option value="5">五年</option>
            <option value="6">六年</option>
            <option value="7">七年</option>
            <option value="8">八年</option>
            <option value="9">九年</option>
            <option value="10">十年</option>
          </select>
        </div>
      </div>
      <p>交易密码： <input type="password" v-model="zfpsw" class="form-control" placeholder="请输入正确的交易密码">
        <p>备注：
          <textarea class="form-control" v-model="remark" placeholder="备注..."></textarea>
        </p>
        <p>
          <button class="btn btn-info" @click="submil_chongzhi">确认充值</button>
        </p>
        <!--<button class="btn btn-info">取消充值</button>-->
    </form>
  </div>
</template>
<script>
import homeTab from '@/components/Tab'
export default {
  name: 'nas_number',
  data() {
    return {
      name: homeTab._username,
      images: [],
      deal_years:'',
      contract_id: '',
      contract_img: '',
      deal_years: '',
      remark: '',
      zfpsw: '',
      deal_gold: '',

    }
  },
  //组件构建
  components: {
    homeTab
  },
  methods: {
    submil_chongzhi: function() {
      console.log(this.images[0])
      let data = {
        'name': homeTab._username, 'zfpsw': this.zfpsw, 'contract_id': this.contract_id,  'contract_img': this.images[0],
        'deal_years': this.deal_years, 'remark': this.remark,'deal_gold': this.deal_gold, 'program_id': 1,'agency_id': 1,
      }
     
      this.$http.post('sz800800.cn/index.php/Agency/deal/add_deal02', data, {
        emulateJSON: true
      }).then(function(res) {
        alert('充值成功')

      }, function(err) {
        alert(JSON.stringify(err))
      })
    },
    addPic(e) {
      e.preventDefault();
      $('input[type=file]').trigger('click');
      return false;
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.createImage(files);
    },
    createImage(file) {
      if (typeof FileReader === 'undefined') {
        alert('您的浏览器不支持图片上传，请升级您的浏览器');
        return false;
      }
      var image = new Image();
      var vm = this;
      var leng = file.length;
      for (var i = 0; i < leng; i++) {
        var reader = new FileReader();
        reader.readAsDataURL(file[i]);
        reader.onload = function(e) {
          vm.images.push(e.target.result);
        };
      }
    },
    delImage: function(index) {
      this.images.shift(index);
    },
    removeImage: function(e) {
      this.images = [];
    },
  }
}
</script>

<style>
* {
  padding: 0px;
  margin: 0px;
}

a {
  text-decoration: none;
}

ul li {
  list-style: none;
}

.header-top {
  width: 100%;
  height: 100%;
  background-color: white;
}

.width-center {
  width: 1000px;
  height: 80px;
  /*border: 1px solid red;*/
  margin: 0px auto;
  line-height: 80px;
}

.header-title {
  float: left;
  font-size: 30px;
}

.header-right {
  float: right;
}







/*选项卡*/

.header {
  width: auto;
  height: 80px;
  /*border: 1px solid red;*/
  text-align: center;
  background-color: #28905a;
}

.header ul li {
  list-style: none;
  display: inline-block;
  font-size: 20px;
  /*border: 1px solid darkblue;*/
  padding: 20px 30px;
  line-height: 38px;
}

.header ul li:hover {
  background-color: royalblue;
  border-radius: 6px;
}

.header ul li a {
  color: white;
  text-decoration: none;
}
</style>



