<template>
    <div style="width:90%;height:auto; margin:20px auto;">
        <homeTab></homeTab>
        
        <form @submit.prevent="submit" class="form-horizontal">
            <div class="form-group">
                <label for="lastname" class="col-sm-1 control-label">商户账号:</label>
                <div class="col-sm-11">
                    <input type="text" v-model="name" class="form-control" id="lastname" placeholder="请输入账号">
                </div>
            </div>
            <div class="form-group">
                <label for="lastname" class="col-sm-1 control-label">商户密码:</label>
                <div class="col-sm-11">
                    <input type="password" v-model="psw" class="form-control" id="lastname" placeholder="请输入密码">
                </div>
            </div>
            <div class="form-group">
                <label class="col-sm-1 control-label">选择模板类型:</label>
                <div class="col-sm-11">
                    <select id="status" @change="model_change" class="form-control" v-model="name_lx">
                        <option disabled value="">请选择</option>
                        <option v-for="item in items">{{item.name}}</option>
                    </select>
                </div>
            </div>
            <div class="form-group">
                <label for="lastname" class="col-sm-1 control-label">选择模板版本:</label>
                <div class="col-sm-11">
                    <select id="banben" @change="banben_change" class="form-control" v-model="name_bb">
                        <option disabled value="">请选择</option>
                        <option v-for="item in releases">{{item.name}}</option>
                    </select>
                </div>
            </div>

            <div class="form-group">
                <label for="lastname" class="col-sm-1 control-label">价格:</label>
                <div class="col-sm-11">
                    <input type="text" readonly="readonly" v-model="muban_price" class="form-control" id="lastname" placeholder="请输入账号">
                </div>
            </div>

            <div class="col-sm-11">
                <div class="btn btn-primary" v-if="yanzheng_btn" v-on:click="submit_yanzheng" id="submit" type="text" disabled>提交1</div>
            </div>
            <div class="col-sm-11">
                <div class="btn btn-primary" v-if="tijiao_btn" v-on:click="submit_shuju" id="submit" type="text" disabled>提交2</div>
            </div>

            <!-- <div class="form-group">
                							<label for="inputPassword" class="col-sm-2 control-label">上传合同附件：</label>
                							<input type="file" name="file" id="doc" style="width:150px;" onchange="javascript:setImagePreview();">
                							<div class="col-sm-10">
                								<img id="preview" src="" style="display:block; width:400px; height:600px;border:1px solid #000000; border-radius:6px;">
                							</div>
                						</div> -->

        </form>
        
    </div>
</template>
<script>
import homeTab from '../../components/Tab'
export default {
    name: 'append',
    data() {
        return {
            items: [],
            releases: [],
            name: '',
            psw: '',
            user_name: '',
            name_lx: '',
            name_bb: '',
            muban_price: '',
            yanzheng_btn: true,
            tijiao_btn: false

        }
    },
    components: {
        homeTab
    },
    created() {
        this.$http.get('sz800800.cn/index.php/Agency/Programadd/program_show', {
            emulateJSON: true
        }).then(function(res) {
            this.items = res.body
            // console.log("成功：" + JSON.stringify(this.items))

        }, function(err) {
            //  alert("失败：" + err.body)
        })
    },
    methods: {
        //提交验证
        submit_yanzheng: function() {
            if(this.name_lx == ''|this.name_bb == ''){
                alert("请选择类型、版本")
                return false
            }
            
            let user_name = homeTab._username
            let data = { 'name': this.name, 'psw': this.psw, 'user_name': user_name,'muban_price':this.muban_price }
            //console.log("data:" + data)
            this.$http.post('sz800800.cn/index.php/Agency/Programadd/check_x', data, {
                headers: {
                },
                emulateJSON: true
            }).then(function(res) {
                if (res.body.status == 1) {
                    // alert(JSON.stringify(res.body.msg))
                    this.submit_shuju()
                    this.yanzheng_btn = false
                    this.tijiao_btn = true
                } else if (res.body.status == 4) {
                    alert(JSON.stringify(res.body.msg))
                } else {
                    alert(JSON.stringify(res.body.msg))
                }

            })
        },
        //提交数据
        submit_shuju: function() {
            let user_name = homeTab._username
            let data = { 'name': this.name, 'user_name': user_name, 'name_lx': this.name_lx, 'name_bb': this.name_bb, 'price': this.muban_price }
            this.$http.post('sz800800.cn/index.php/Agency/Programadd/program_add', data, {
                headers: {
                },
                emulateJSON: true
            }).then(function(res) {
                console.log(res.body)
                alert("提交成功，等待审核···")

            })
        },
        // 获取模板下拉列表的值执行的函数
        model_change: function() {
            let that = this;
            let a = $("#status").val();
            console.log("status:" + a)
            let data = { 'name': a }
            this.$http.post('sz800800.cn/index.php/Agency/Programadd/program_b', data, {
                emulateJSON: true
            }).then(function(res) {
                that.releases = res.body

            }, function(err) {
                //  alert("失败：" + err.body)
            })
        },
        //获取版本选择下拉列表的值执行的函数
        banben_change: function() {
            let that = this;
            let a = $("#banben").val();
            console.log("banben:" + a)
            let data = { 'name': a }
            this.$http.post('sz800800.cn/index.php/Agency/Programadd/program_b_p', data, {
                emulateJSON: true
            }).then(function(res) {
                that.muban_price = res.body
            }, function(err) {
                //  alert("失败：" + err.body)
            })
        }

    }

}
</script>
<style>

</style>




