                  <tr>
                    <th style="width:8%;">序号</th>
                    <th style="width:12%;">服务商</th>
                    <th style="width:12%;">客户</th>
                    <th style="width:12%;">小程序</th>
                    <th style="width:8%;">快捷通知</th>
                    <th style="width:8%;">邮箱</th>
                    <th style="width:8%;">状态</th>
                    <th style="width:18%;">操作</th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="active" v-for="item in items">
                    <td>{{item.xuhao}}</td>
                    <td>{{item.user_company}}</td>
                    <td>{{item.realname}}</td>
                    <td> <router-link to="/client">{{item.appmcheng}}</router-link></td>
                    <td>{{item.quick_notice}}</td>
                    <td>{{item.mail}}</td>
                    <td>{{item.state}}</td>
                  
                    <td>
                      <router-link to="/nas_order">编辑</router-link>|
                      <router-link to="/nas_order">小程序</router-link>|
                      <a to="javascript:void(0)" v-on:click="jinzhi">禁止</a>|
                      <router-link to="/Recharge">充值</router-link>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </litd>
        <!--添加用户-->
        <li class="item" id="cont2" style="display:none;">
          <div class="box">
            <form class="form-horizontal ajaxfrom" id="add_user" name="add_user" enctype="multipart/form-data">
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">用户名</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="kehuname" type="text" style="ime-mode:disabled" class="form-control" value="">
                  <span class="help-block" style="color:palevioletred;">请您输入用户名，用户名为 3 到 15 个字符组成，包括汉字，大小写字母（不区分大小写）</span>
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">密码</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="psw" type="password" class="form-control" autocomplete="off">
                  <span class="help-block" style="color:palevioletred;">请您输入密码，最小长度为 8 个字符</span>
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">确认密码</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="psw1" type="password" class="form-control" autocomplete="off">
                  <span class="help-block" style="color:palevioletred;">请您再次输入密码，确认密码正确输入</span>
                </div>
              </div>

              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">客户姓名</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="realnames" required="" type="text" class="form-control" value="" autocomplete="off">
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">身份证号</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="idcard" required="" type="text" class="form-control" value="" autocomplete="off">
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">手机号</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="tel" required="" type="text" class="form-control" value="" autocomplete="off">
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">邮箱</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="mail" type="email" required="" class="form-control" value="" autocomplete="off">
                </div>
              </div>
              <!-- <div class="form-group">
                  <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">身份证正面</label>
                  <div class="col-sm-10 col-lg-9 col-xs-12">
                    <div class="input-group ">
      
                     <input type="file" name="idcard1"   ref="avatarInput"> 
                      <input type="file" name="idcard1" ref="avatarInput">
                      <div class="image_container">
                        <img id="preview1" width="140" height="90">
                      </div>
                    </div>
                  </div>
                </div>
                <div class="form-group">
                  <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">身份证反面</label>
                  <div class="col-sm-10 col-lg-9 col-xs-12">
                    <div class="input-group ">
                      <input name="idcard2" type="file">
                      <div class="image_container">
                        <img id="preview2" width="140" height="90">
                      </div>
                    </div>
                  </div>
                </div> -->
              <div class="form-group">
                <div style="text-align:center;">
                  <button @click="submil_zhuce" class="btn btn-primary span3" id="add_btn">确认注册</button>
                </div>
              </div>
            </form>
          </div>
        </li>4012
      </ul>
      <div class="bottom">
        <p>版权所有：2017深恒利信息技术有限公司</p>
      </div>
    </div>
  </div>
</template>
<script src="https://cdn.bootcss.com/jquery/2.1.4/jquery.min.js"></script>
<script>
import homeTab from '@/components/Tab'
export default {
  name: 'nas_number',
  data() {
    return {
      items: [],
      name: homeTab._username,
      kehuname:'',
      psw: '',
      psw1: '',
      mail: '',
      realnames: '',
      idcard: '',
      tel: '',
      idcard1: '',
      idcard2: '',
      agency_id:'',
      
      str:'',
    }
  },
  //组件构建
  components: {
    homeTab
  },
  //客户管理渲染
    created() {
      var _this = this
      this.$http.get('sz800800.cn/index.php/Agency/user/user_list?id=1', {
        emulateJSON: true
      }).then(function(res) {
        // console.log("客户管理-渲染" + JSON.stringify(res.body))
        
        _this.items = res.body;
        for(var i=0;i<res.body.length;i++){
          this.index = i;
        }
      }, function(err) {
        //  alert("失败：" + err.body)
    
    })
  },
  methods: {
    //Tab切换
    showTab: function(id) {
      var a = function(id) {
        return document.getElementById(id);
      }
      for (var i = 1; i < 3; i++) {
        if (id == i) {
          a("cont" + i).style.display = "block";
        } else {
          a("cont" + i).style.display = "none";
        }
      }
      return false;
    },
    //客户管理搜索
    submit_sousuo: function() {
      let data = { 'id': 1, 'str': this.str }
      console.log("搜索地址："+'sz800800.cn/index.php/Agency/user/user_search?id=1+&'+this.str)
      this.$http.post('sz800800.cn/index.php/Agency/user/user_search', data, {
        emulateJSON: true
      }).then(function(res) {
        // alert(JSON.stringify(res.body))
         this.items = res.body
       // alert(JSON.stringify(res.body))
        if (res.body.status == 1) {
           //alert("搜索成功："+res.body)
          //提交后清空
          this.$data = Object.assign(this.$data, this.defaultData);
        
        }
      }, function(err) {
        alert("搜索失败："+err.body.msg)
      })
    },
    // getFile(event) {
    //               this.file = event.target.files[0];
    //               console.log(this.file);
    // },
//客户管理用户注册
    submil_zhuce: function() {
      //取消事件的默认动作。
      // event.preventDefault();
      let data = {
        'name': this.kehuname, 'psw ': this.psw, 'psw1': this.psw1
        , 'mail': this.mail, 'realname':this.realnames, 'idcard': this.idcard
        , 'tel': this.tel, 'agency_id': 1
      }
            this.$http.post('sz800800.cn/index.php/Agency/user/add_user',data,{
        headers: {
        },
        emulateJSON: true
    }).then(function(res) {
     
      //alert("msg:"+res.body.msg)
        if (res.status === 200&res.body.status==1|res.body.status==1.1) {
          //  alert('添加成功')
           alert('添加成功')
          cont1.style.display = "block"
          cont2.style.display = "none";
        }
      })
    },
    jinzhi: function(){
       let data = {'id':1,'state':1}  
            /*接口请求*/
            this.$http.get('sz800800.cn/index.php/Agency/user/jinzhi',data,{
        headers: {
        },
        emulateJSON: true
    }).then(function(res) {
      if(res.body.status!=-1){
          alert(res.body.state)
          }else{alert("已启用。")}
			},function(err) {
        alert("网络错误，请稍后重试。")
            console.log(JSON.stringify(err))
          });return false
      }
    }
  
}
</script>

<style>
/*内容*/

.tab-cont .item {
  width: 100%;
  height: 100%;
  color: #000;
}

.item .box {
  width: 100%;
  min-height: 450px;
  padding-bottom: 20px;
  background-color: white;

}

.box-bor {
  width: 90%;
  height: auto;
  margin: 0px auto;
  border-radius: 10px;
  padding-top: 40px;
  color: #333333;
}
.btn-primary{
  margin-left:48%;
  margin-top: 20px 
}
</style>



