<template>
  <!-- 小程序审核管理 -->
  <div class="shenghe">
    <ul class="tab-cont__wrap">
      <!--客户管理-->
        <div class="box">
          <div class="box-bor">
            <h1>微信小程序审核管理</h1>
            <table class="table">
              <thead>
                <tr>
                  <th style="width:8%;">编号</th>
                  <th style="width:8%;">小程序名称</th>
                  <th style="width:8%;">服务商公司</th>
                  <th style="width:8%;">商户账号</th>
                  <th style="width:8%;">模板类型</th>
                  <th style="width:8%;">价格</th>
                  <th style="width:8%;">审核状态</th>
                  <th style="width:8%;">申请时间</th>
                  <!-- <th style="width:18%;">操作</th> -->
                </tr>
              </thead>
              <tbody>
                <tr class="active" v-for="item in items">
                  <td>{{item.id}}</td>
                  <td>未通过</td>
                  <td>{{item.agency_name}}</td>
                  <td>
                    <router-link to="/client">{{item.shagnhu_name}}</router-link>
                  </td>
                  <td>{{item.muban_name}}</td>
                  <td>{{item.muban_price}}</td>
                  <td>{{item.state}}</td>
                  <td>{{item.add_time}}</td>
                  <!-- <td>
                    <router-link to="/nas_order">编辑</router-link>|
                    <router-link to="/nas_order">小程序</router-link>|
                    <a to="javascript:void(0)">禁止</a>|
                    <router-link to="/Recharge">充值</router-link>
                  </td> -->
                </tr>
              </tbody>
            </table>
          </div>
        </div>
    </ul>
    <div class="bottom">
      <p>版权所有：2017深恒利信息技术有限公司</p>
      <!-- <span>需求创建时间{{1504345897 | time}}</span>
       this.filter('time', function (value) {
        return new Date(parseInt(value) * 1000).toLocaleString().replace(/年|月/g, "-").replace(/日/g, " ");
    }) -->
    </div>
  </div>
</template>
<script src="https://cdn.bootcss.com/jquery/2.1.4/jquery.min.js"></script>
<script>
import homeTab from '@/components/Tab'
export default {
  name: 'shenghe',
  data() {
    return {
      items: [],
      aa:homeTab._username,
    }
  },
  //组件构建
  components: {
    homeTab
  },
  //客户管理渲染
      created() {
           
        var _this = this
        this.$http.get('http://123.207.41.93/jtct/index.php/Agency/Programadd/shenhe_resaule?name='+_this.aa, {
                emulateJSON: true
            }).then(function(res) {
                // console.log("成功aaa：" + JSON.stringify(res.body))
                _this.items = res.body;
            }, function(err) {
                //  alert("失败：" + err.body)
            
        })
    }
}
</script>

<style>
/*内容*/

.tab-cont .item {
  width: 100%;
  height: 100%;
  color: #000;
}

.item .box {
  width: 100%;
  min-height: 450px;
  padding-bottom: 20px;
  background-color: white;
}

.box-bor {
  width: 90%;
  height: auto;
  margin: 0px auto;
  border-radius: 10px;
  padding-top: 40px;
  color: #333333;
}

.btn-primary {
  margin-left: 48%;
  margin-top: 20px
}
</style>



