
<template>
  <div id="home">
    <homeTab></homeTab>
    <!--tab-->

    <div class="tabzhanghao">
      <div id="tab1" class="tab1">
        <a href="javascript:void(0)" v-on:click="showTab('1')">账号信息</a>
      </div>
      <div id="tab2" class="tab2">
        <a href="javascript:void(0)" v-on:click="showTab('2')">交易安全</a>
      </div>
    </div>
    <!-- 账号管理-->
    <!-- 安全管理-->
    <div id="cont1" style="display:block;">
      <form id="form" name="form">
        <p>管理员帐号:<input type="text" id="user_name" v-model="user_name"></p>
        <p>管理员密码: <input type="password" v-model="user_psw" value=""></p>
        <p>新密码: <input type="password" v-model="psw1" value=""></p>
        <p>确认密码: <input type="password" v-model="psw2" value=""></p>
        <p> 服务商: 深恒利信息技术有限公司</p>
        <input v-on:click="submit" type="button" value="确认提交">
      </form>
    </div>

    <!--交易安全-->
    <div id="cont2" style="display:none;">
      <form id="zfform" name="zfform">
        <p>管理员帐号:<input type="text" id="user_name1" v-model="zfuser_name"></p>
        <p>原交易密码: <input type="password" v-model="zfuser_psw" value=""></p>
        <p>新交易密码: <input type="password" v-model="zfpsw1" value=""></p>
        <p>确认交易密码: <input type="password" v-model="zfpsw2" value=""></p>
        <p> 服务商: 深恒利信息技术有限公司</p>
        <input v-on:click="zfsubmit" type="button" value="确认提交">
      </form>
    </div>
  </div>
</template>

 
<script>
import homeTab from '@/components/Tab'
export default {
  name: 'home',
  data() {
    return {
      user_name: '',
      user_psw: '',
      psw1: '',
      psw2: '',

      zfuser_name: '',
      zfuser_psw: '',
      zfpsw1: '',
      zfpsw2: '',
    }
  },
  //组件构建
  components: {
    homeTab
  },
  //获取当前表单
  created() {
    this.user_name = homeTab._username;
    this.zfuser_name = homeTab._username;
    this.defaultData = JSON.parse(JSON.stringify(this.$data));
    // let that = this 
    // let _username = that.$route.params.username
    // console.log("_username:::" + _username)
  },
  // _username,
  methods: {
    showTab: function(id) {
      var a = function(id) {
        return document.getElementById(id);
      }
      for (var i = 1; i < 3; i++) {
        if (id == i) {
          a("cont" + i).style.display = "block";
        } else {
          a("cont" + i).style.display = "none";
        }
      }
      return false;
    },
    zfsubmit: function() {
      let data = { 'name': this.zfuser_name, 'psw': this.zfuser_psw, 'psw1': this.zfpsw1, 'psw2': this.zfpsw2 }
      this.$http.post('sz800800.cn/index.php/Agency/updpsw/updzfpsw', data, {
        emulateJSON: true
      }).then(function(res) {
        alert(res.body.msg)
        if (res.body.status == 1) {
          //提交后清空
          this.$data = Object.assign(this.$data, this.defaultData);
          alert(res.body.msg)
        }
      }, function(err) {
        alert(err.body.msg)
      })
    },
    submit: function() {
      let data = { 'name': this.user_name, 'psw': this.user_psw, 'psw1': this.psw1, 'psw2': this.psw2 }
      this.$http.post('sz800800.cn/index.php/Agency/updpsw/updpsw', data, {
        emulateJSON: true
      }).then(function(res) {
        // alert(res.body.msg)
        if (res.body.status == 1) {
          //提交后清空
          this.$data = Object.assign(this.$data, this.defaultData);
          alert(res.body.msg)
        }
      }, function(err) {
        alert(err.body.msg)
      })
    }
  }
}
</script>
 
<style>
.tabzhanghao{
  margin-bottom: 120px
}

</style>


