<template>
    <div class="tab" js-tab="1">
        <homeTab></homeTab>
        <div class="tabzhanghao">
            <div id="tab1" class="tab1">
                <a to="javascript:void(0)" v-on:click="showTab('1')">消费记录</a>
            </div>
            <div id="tab2" class="tab2">
                <a to="javascript:void(0)" v-on:click="showTab('2')">充值记录</a>
            </div>
        </div>
        <div class="tab-cont">
            <ul class="tab-cont__wrap">
                <!--消费记录-->
                <li class="item" id="cont1" style="display:block;">
                    <div class="box">
                        <div class="box-bor">
                            <table class="table">
                                <caption style="text-align:center;font-size:26px;">我的消费记录</caption>
                                <thead>
                                    <tr>
                                        <th style="width:6%;">流水号</th>
                                        <th style="width:10%;">客户</th>
                                        <th style="width:10%;">平台类型</th>
                                        <th style="width:10%;">合作平台</th>
                                        <th style="width:10%;">交易类型</th>
                                        <th style="width:10:%;">交易金额</th>
                                        <th style="width:10%;">可用余额</th>
                                        <th style="width:12%;">交易日期</th>
                                        <th style="width:6%;">充值状态</th>
                                        <th style="width:16%;">备注</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr class="active" v-for="item in xfitems">
                                        <td>{{item.running_num}}</td>
                                        <td>{{item.realname}}</td>
                                        <td>小程序</td>
                                        <td>{{item.appmcheng}}</td>
                                        <td>{{item.deal_type}}</td>
                                        <td>{{item.deal_gold}}</td>
                                        <td>{{item.balance}}</td>
                                        <td>{{item.time1}}</td>
                                        <td>{{item.state}}</td>
                                        <td>{{item.appmcheng}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </li>
                <!--充值记录-->
                <li class="item" id="cont2" style="display:none;">
                    <div class="box">
                        <div class="box-bor">
                            <table class="table">
                                <caption style="text-align:center;font-size:26px;">我的充值记录</caption>
                                <thead>
                                    <tr>
                                        <th style="width:5%;">序号</th>
                                        <th style="width:10%;">充值账号</th>
                                        <th style="width:8%;">充值金额</th>
                                        <th style="width:8%;">充值日期</th>
                                        <th style="width:10%;">是否充值成功</th>
                                        <th style="width:6%;">备注</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr class="active" v-for="item in czitems">
                                        <td>{{item.state}}</td>
                                        <td>{{item.user_company}}</td>
                                        <td>{{item.recharge_gold}}</td>
                                        <td>{{item.recharge_time}}</td>
                                        <td>{{item.state}}</td>
                                        <td>{{item.content}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </li>
            </ul>
            <div class="bottom">
                <p>版权所有：2017深恒利信息技术有限公司</p>
            </div>
        </div>
    </div>
</template>
<script>
import homeTab from '@/components/Tab'
import { formatDate } from '../../assets/js/date'
export default {
    name: 'nas_wallet',
    data() {
        return {
            xfitems:[],
            recharge_gold: '',
            recharge_time: '',
            user_company: '',
            content: '',
            company: '',
            state: '',
            czitems:[],
            running_num: '',
            deal_type: '',
            deal_gold: '',
            balance: '',
            dj_balance: '',
            deal_time: '',
            time: '',
            time1: '',
            state: '',
            appmcheng: '',
            realname: '',

        }
    },
    //组件构建
    components: {
        homeTab
    },
    created() {
        //充值记录
        this.$http.get('sz800800.cn/index.php/Agency/recharge/recharge_list?id=1', {
            emulateJSON: true
        }).then(function(res) {
            this.czitems = res.body
            console.log("成功：" + JSON.stringify(res.body))
        }, function(err) {
            //  alert("失败：" + err.body)
        }),

        //消费记录
            this.$http.get('sz800800.cn/index.php/Agency/deal/deal_list?id=1', {
                emulateJSON: true
            }).then(function(res) {
                this.xfitems = res.body
                console.log("返回数据：" + JSON.stringify(res.body))
            }, function(err) {
                //  alert("失败：" + err.body)
            })
    },
    methods: {
        showTab: function(id) {
            var a = function(id) {
                return document.getElementById(id);
            }
            for (var i = 1; i < 3; i++) {
                if (id == i) {
                    a("cont" + i).style.display = "block";
                } else {
                    a("cont" + i).style.display = "none";
                }
            }
            return false;

        },
        filters: {
            formatDate(time) {
                var data = data;
                return formatDate(data, 'yyyy-MM-dd hh:mm');
            },
            formatDate(time1) {
                var data1 = data1;
                return formatDate(data1, 'yyyy-MM-dd hh:mm');
            }
        }
    }
}
</script>
<style>
.tab_title {
    margin: 60px;
    padding: 20px
}
.box{
    margin: 40px;
}
</style>



