import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login/login'
import Home from '@/views/Home/Home'//主页
import Nas_number from '@/views/number/nas_number'//客户管理---------
import Client from '@/views/number/client'//客户管理编辑客户
import Recharge from '@/views/number/recharge'//客户管理充值
import Nas_wallet from '@/views/wallet/nas_wallet'//钱包-----------
import Nas_order from '@/views/order/nas_order'//小程序管理-------------
import Contract from '@/views/order/contract'//合同信息
import Edit from '@/views/order/edit'//参数编辑
import Append from '@/views/order/append'//小程序基本信息
// import Shenghe from '@/views/order/shenghe'//审核

import {delCookie,getCookie} from '@/assets/js/cookie'
Vue.use(Router)

const routes = [
    {
     // path: '/login',
      path: '/',
      name: 'Login',
      component: Login,
      meta:{requireAuth: true }
    },
    {
      // path:'/home',
      path:'/home',
      name:'Home',
      component: Home,
      meta:{requireAuth: true }
    },
    {
      //path:'/',
      path:'/nas_number',
      name:'Nas_number',
      component:Nas_number,
      meta:{requireAuth: true }
    },
    {
      path:'/client',
      name:'Client',
      component:Client,
      meta:{requireAuth: true }
    },
    {
      path:'/recharge',
      name:'Recharge',
      component:Recharge,
      meta:{requireAuth: true }
    },
    {
      path:'/nas_wallet',
      name:'Nas_wallet',
      component:Nas_wallet,
      meta:{requireAuth: true }
    },
    {
      path:'/nas_order',
      name:'Nas_order',
      component:Nas_order,
      meta:{requireAuth: true }
    },
    // {      
    //   path:'/shenghe',
    //   name:'Shenghe',
    //   component:Shenghe,
    //   meta:{requireAuth: true }
    // },
    {
        path:'/contract',
        name:'Contract', 
        component:Contract,
        meta:{requireAuth: true }
    },  
    {
      path:'/edit',
      name:'Edit',
      component:Edit,
      meta:{requireAuth: true }      
    },
    {
      path:'/append',
    //  path:'number',
      name:'Append',
      component:Append,
      meta:{requireAuth: true }
    }
  ];
  const router = new Router({
    // mode:'history',
    // base:'hot',
    routes
});

export default router


